const buildURL = function(condition, status, ordering = '-add_time') {
  let res = '?format=json&ordering=' + ordering + '&'
  
  if (JSON.stringify(condition) == '{}') {
    
  } else {

    for (const k in condition) {
      const item = k + '=' + condition[k] + '&'
      res += item
    }

  }

  if (status) {
    res += 'status=True'
  } else {
    res += 'status='
  }

  return res
}

const getOffset = function (url, def_offset) {
  if (url == null) { return def_offset }

  let res = url.split('?')[1].split('&')
  for (const i of res) {
    const e = i.split('=')
    if (e[0] == 'offset') {
      return e[1]
    }
  }
  return def_offset
}

export default {
  buildURL
  ,
  buildFirstPagerUrl(api_start, condition, init_pager = null, status = true, ordering = '-add_time') {
    let res = { }
    if (init_pager != null) {
      res = { ...condition, ...init_pager }
    } else {
      res = { ...condition }
    }

    return api_start + buildURL(res, status, ordering)
  },
  getNowPage(offset, limit) {
    if (offset == 0) { return 1 }
    let res = (Math.floor(offset / limit) + 1)

    if (isNaN(res)) { return 1 }
    return res
  },
  getTotalPage(limit, count) {
    if (count == 0) { return 1 }
    let res = Math.abs(Math.ceil(count / limit))
    return res
  },
  getAuth() {
    return {
      username: 'VcrTing',
      password: 'ZT123zlt'
    }
  },
  buildPageLink(url, count, limit, next, previous) {
    const pageLink = { }

    pageLink.count = count
    pageLink.next = next
    pageLink.previous = previous
    pageLink.offsetNow = getOffset(url)
    pageLink.limit = limit

    return pageLink
  }
}


