<template>
  <button class="btn btn-info btn-sm pb-0 float-right" @click="sortOption">
    <i class="fa fa-toggle-down " v-if="!isSort"></i>
    <i class="fa fa-toggle-up" v-else></i>
  </button>

</template>

<script>
import tool from '@/common/es/tool'

export default {
  name: 'SortOption',
  props: {
    _list: {
      type: Array
    }
  },
  data() {
    return {
      isSort: false
    }
  },
  methods: {
    sortOption() {
      this._list.reverse()
      this.isSort = !this.isSort
      if (this.isSort) { tool.showToast('逆序排列') } else { tool.showToast('顺序排列') }
    },
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/listing.sass"

</style>


